body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
    font-size: 2em;
    font-family: "Myriad Pro";
    color: black;
    line-height: 0.6;
    text-align: center;
}

h2 {
  font-size: 1.1em;
  font-family: "Myriad Pro";
  color: rgb(34, 34, 34);
  line-height: 0.6;
  text-align: center;
}

h3 {
  font-size: 1.3em;
  font-family: "Myriad Pro ";
  color: rgb(34, 34, 34);
  line-height: 0.6;
  text-align: center;
}

p {
    font-size: 1em;
    font-family: "Helvetica Neue";
    color: rgb(132, 132, 132);
    line-height: 1.625;
    text-align: center;
}

i {
    font-size: 1.5em;
    font-family: "Helvetica Neue";
    color: rgb(132, 132, 132);
    line-height: 1.333;
    text-align: center;
}


